/*********************
LAZYLOADING IMAGES - uses lazysizes script
*********************/
IMG[src=''], IMG:not([src]) {
  opacity: 0; }

.lazyhd,
.lazysquare,
.lazy70 {
  position: relative;
  height: 0;
  background: #eee; }
  .lazyhd img,
  .lazysquare img,
  .lazy70 img {
    width: 100%;
    height: auto;
    opacity: 0.2;
    transition: opacity 0.6s ease-in; }

.lazyhd {
  padding-bottom: 56.25%; }

.lazysquare {
  padding-bottom: 100%; }

.lazy70 {
  padding-bottom: 70%; }

.lazyload {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden; }

.lazyloading {
  opacity: 0.2; }

.lazyloaded {
  opacity: 1 !important; }

